@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap');

.outer-patent-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #03174F;
    padding: 2rem 0rem 2rem 0rem;
}
.patents-container{
    display: flex;
    justify-content: space-evenly;
    /* padding: 1rem; */
    width: 100%;
}
.patent-container {
    width: 25%;   
}
.patent-container img {
    height: 26rem;
    width: 100%;
}
.patent-main-heading{
    border-bottom: 1px solid white;
    font-family: 'Montserrat', sans-serif;
    padding-bottom: 1rem;
    color: white;
    font-size: 2.2rem;
}
.see_more{
    color: rgb(0, 183, 255);
    margin-right: -89%;
    margin-top: 1rem;
}
.see_more:hover{
    cursor: pointer;
}